export default [
  {
    title: '',
    dataIndex: 'key',
    render(text, record) {
      let num = record.key + ""
      if (num.length < 8) {
        let str = ""
        for (let i = 0; i < 8 - num.length; i++) {
          str += "0"
        }
        return str + record.key
      }
      return record.key
    }
  }, {
    title: '',
    dataIndex: 'name'
  }, {
    title: '',
    dataIndex: 'ifJoin',
    render(text, record) {
      var status = ""
      switch (record.ifJoin) {
        case 1:
          status = ""
          break;
        case 0:
          status = ""
          break;
        default:
          status = ""
          break;
      }
      return status
    }
  }, {
    title: '',
    dataIndex: 'city'
  }, {
    title: '',
    dataIndex: 'higherUp'
  }, {
    title: '',
    dataIndex: 'createAt',
    render(text, record) {
      return new Date(record.createAt).toISOString().slice(0, 10)
    }
  }
]